export default {
  data() {
    return {};
  },
  computed: {
    userName: {
      get() { //  
        return this.$store.state.dreamObj.userName
      },
      set(userName) { // 
        this.$store.commit('updateDream', { userName })
      }
    },
    dream: {
      get() { //  
        return this.$store.state.dreamObj.dream
      },
      set(dream) { // 
        this.$store.commit('updateDream', { dream })
      }
    },
  },
  methods: {
    clickSelected(type) {
      if (type === this.select) {
        this.select = '';
        return;
      }
      this.select = type;
    },
    showError(msg) {
      this.error = msg;
      setTimeout(() => {
        this.error = '';
      }, 2000)
      alert(msg);
    },
    onclickButton() {
      if (!this.$store.state.dreamObj.userName) {
        this.showError('');
        return;
      }
      if (!this.$store.state.dreamObj.dream) {
        this.showError('');
        return;
      }
      $(".name").blur(() => {

      });

      setTimeout(() => {
        this.$router.push('address');
      }, 100)
    },
  },
  mounted() {

  }
};



