const MPReg = /^(13|15|18|14|17|16|19)\d{9}$/;

export default {
  data() {
    return {
      isClick: false,
      isClickToRight: false,
      index: -1,

      // userName:'',
      // dream:'',
      province: '',
      city: '',
      county: '',
      address: '',
      mobile: '',

      alertText:'',
      isShowAlert: false,
    };
  },
  computed: {
    userName: {
      get() { //  
        return this.$store.state.dreamObj.userName
      },
      set(userName) { // 
        this.$store.commit('updateDream', { userName })
      }
    },
    dream: {
      get() { //  
        return this.$store.state.dreamObj.dream
      },
      set(dream) { // 
        this.$store.commit('updateDream', { dream })
      }
    },
  },
  methods: {
    clickSelected(type) {
      if (type === this.select) {
        this.select = '';
        return;
      }
      this.select = type;
    },
    showError(msg) {
      this.error = msg;
      setTimeout(() => {
        this.error = '';
      }, 2000)
      alert(msg);
    },
    onclickButton() {
      if (!this.$store.state.dreamObj.userName) {
        this.showError('');
        return;
      }
      if (!this.$store.state.dreamObj.dream) {
        this.showError('');
        return;
      }
      $(".name").blur(() => {

      });

      setTimeout(() => {
        this.$router.push('address');
      }, 100)
    },
    onclickPostcard(index) {
      this.index = index;
      this.isClick = true;
    },
    onclickButton() {
      if (!this.isClickToRight) {  // 

        if (!this.$store.state.dreamObj.userName) {
          this.showError('');
          return;
        }
        if (!this.$store.state.dreamObj.dream) {
          this.showError('');
          return;
        }
        this.isClickToRight = true;
        return;
      }

      if (!this.province) {
        this.showError('');
        return;
      }
      if (!this.city) {
        this.showError('');
        return;
      }
      if (!this.county) {
        this.showError('');
        return;
      }
      if (!this.address) {
        this.showError('');
        return;
      }
      if (!this.mobile) {
        this.showError('');
        return;
      }

      if (!MPReg.test(this.mobile)) {
        this.showError('');
        return;
      }

      $(".province").blur(() => {

      });

      // 
      this.saveDream();

      setTimeout(() => {
        this.$router.push({
          path: 'result',
          query: {
            index: this.index,
          }
        });
      }, 500)
      // setTimeout(() => {
      //   this.$router.push('result');
      // }, 500)

    },

    saveDream() {
      const scriptEle = document.createElement('script');
      scriptEle.src = `https://impservice.youdao.com/shutup/submit.s?
      phone=${this.mobile}&name=${this.userName}&
      email=${this.province}${this.city}${this.county}/&
      area=${this.dream}&address=${this.address}&
      age=${this.index}&from=lettertomyself`;
      document.body.appendChild(scriptEle);
    },
    clickMash() {
      this.isShowAlert = false;
    },
    showError(msg) {
      this.isShowAlert = true;
      this.alertText = msg;
    },
  },
  mounted() {

  }
};



